import { createApp } from 'vue'
import App from './App'
import router from './router'
import store from './store/'
import VueLazyload from 'vue-lazyload'
import infiniteScroll from 'vue-infinite-scroll'
import { userInfo } from './api'
import { ElButton, ElPagination, ElCheckbox, ElIcon, ElAutocomplete, ElLoading, ElNotification, ElMessage, ElSteps, ElStep, ElTable, ElOption, ElTableColumn, ElInput, ElDialog, ElSelect } from 'element-plus'
import { getStore } from './utils/storage'
import VueContentPlaceholders from 'vue-content-placeholders'

const app = createApp(App)
app.use(VueContentPlaceholders).use(ElButton).use(ElPagination).use(ElCheckbox)
.use(ElIcon).use(ElAutocomplete).use(ElSteps).use(ElStep).use(ElTable).use(ElTableColumn).use(ElInput).use(ElDialog)
.use(ElSelect).use(ElLoading).use(ElOption)
app.config.globalProperties.$loading = ElLoading.service
app.config.globalProperties.$notify = ElNotification
app.config.globalProperties.$message = ElMessage
app.use(infiniteScroll)
app.use(VueLazyload, {
  // preLoad: 1.3,
  // error: 'dist/error.png',
  loading: require('./static/images/load.gif')
  // attempt: 1
}).use(router).use(store)

const whiteList = ['/home', '/goods', '/login', '/register', '/goodsDetails', '/thanks', '/search', '/refreshsearch', '/refreshgoods'] // 不需要登陆的页面
router.beforeEach(function (to, from, next) {
  let params = {
    params: {
      token: getStore('token')
    }
  }
  // userInfo(params).then(res => {
    
  // })
  if(getStore('token')) {
    store.commit('RECORD_USERINFO', {info: {
      token: getStore('token'),
      userId: getStore('userId'),
      ...JSON.parse(getStore('userInfo')).info
    }})
    if (to.path === '/login') { //  跳转到
      next({path: '/'})
      return false
    }
    next()
  } else {
    if (whiteList.indexOf(to.path) !== -1) { // 白名单
      next()
    } else {
      next({
        path: '/login',
        query: {
          path: from.path,
          query: JSON.stringify(from.query)
        }
      })
    }
  }
})
/* eslint-disable no-new */
app.mount('#app');
