import { httpGet, httpPost } from './public'
import { getStore } from '@/utils/storage'
import { notLoginUserinfo, navLists, loginUserinfo, HomeData, RegisterData, LoginData, RecommendData, ThankData } from './data'
// 登陆
export const userLogin = (params) => {
  return Promise.resolve({
    code: 200,
    result: {
      id: params.userName,
      token: params.userName + params.userPwd,
      state: 1,
      info: {
        username: params.userName,
        file: '//s02.mifile.cn/assets/static/image/logo-mi2.png'
      }
    }
  })
  // return httpPost('/member/login', params)
}
// 退出登陆
export const loginOut = (params) => {
  return Promise.resolve({
    code: 200,
    ersult: {
      state: 1
    }
  })
  // return httpGet('/member/loginOut', params)
}
// 用户信息
export const userInfo = (params) => {
  let res = notLoginUserinfo
  const token = getStore('token')
  if(token) {
    res = loginUserinfo
  }
  return Promise.resolve(res)
}
