import { createStore } from 'vuex'
import mutations from './mutations'
import action from './action'
import { getStore } from '@/utils/storage'

const state = {
  login: !!getStore('token'),   // 是否登录
  userInfo: JSON.parse(getStore('userInfo')) || {}, // 用户信息
  cartList: JSON.parse(getStore('buyCart')) || [],   // 加入购物车列表
  showMoveImg: false, // 显示飞入图片
  elLeft: 0,
  elTop: 0,
  moveImgUrl: null,
  cartPositionT: 0, // 购物车位置
  cartPositionL: 0,
  receiveInCart: false, // 是否进入购物车
  showCart: false // 是否显示购物车
}

export default createStore({
  state,
  action,
  mutations
})
